@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@600&display=swap);
*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Quicksand', sans-serif;
}

.main_div{
    width: 100%;
    height: 100vh;
    background: #6983aa;
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items:center;
    text-align:center;
}

.center_div{
    width: 25%;
    height: 80vh;
    background:#f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0,0,0,0.5);
    border-radius:15px;
}

h1{
    color: white;
    background-color: #8566aa;
    padding: 6px 0 2px 0;
    margin-bottom: 10px;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

input{
    text-align: center;
    height:30px;
    width: 60%;
    top: 10px;
    border: none;
    border-bottom: 2px solid #8566aa;
    background:transparent;
    font-size:20px;
    font-weight: 500;
    outline: none;
}

button{
    min-height: 40px;
    width: 40px;
    border-radius: 50%;
    background: #8566aa;
    color: #fff;
    font-size:40px;
    border: none;
    outline: none;
    margin-left: 10px;
    text-align: center;
    padding: 0  0 3px 0;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
    cursor: pointer;
}

button:hover{
    transition: 0.35s;
    background: #20bf6b;
}

ol{
    margin-top:30px;
}

ol li{
    padding-left: 0px;
    text-align: left;
    font-size: 20px;
    font-weight:500;
    min-height: 40px;
    display:flex;
    align-items: center;
    color: #8566aa;
    text-transform: capitalize;
}

.todo_style{
    display: flex;
    flex-direction:row;
    justify-content: left;
    align-items:center;
}

.todo_style .fa-times{
    width: 20px;
    height: 20px;
    display: flex;
    justify-content:center;
    align-items:center;
    background: #8566aa;
    border-radius: 50%;
    margin: 0 15px 0 35px;
    color: aliceblue;
    box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}

.todo_style .fa-times:hover{
    background: crimson;
}

.todo_style .fa-times:hover .list_style {
    color: crimson;
}
